*, *::after, *::before {
  box-sizing: border-box;
}
html {
  height: 100%;
}
body, p {
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
  color: inherit;
}
nav, ul{
  padding: 0;
  margin: 0;
  list-style: none;
}
li {
  text-decoration: none;
}
h1, h2, h3, h4, h5, h6 {
  display: block;
  margin: 0;
}